@media screen and (max-width: 600px){
    header {
      padding:0;
    }
    header .right-header {
      padding: 2rem  !important;
    }
    .theme-btn {
      width:50px;
      height:50px;
    }
    .header-content{
      grid-template-columns: 1fr;
      padding-bottom:6rem;
    }
    .left-header{
      .h-shape{
          display:none;
      }
      .right-header{
          grid-row:1;
          width:90%;
          margin:0 auto;
          .name {
              font-size:2.55px;
          }
      }
    }
    .controlls{
      top:auto;
      bottom:0;
      flex-direction:row;
      justify-content:center;
      left:50%;
      transform:translateX(-50%);
      width:100%;
      background-color: grey;
      
    }
  
}